import "./App.css";
import { useEffect, useMemo, useState } from "react";
import Start from "./components/Start";
// import Timer from "./components/Timer";
import Trivia from "./components/Trivia";

function App() {
  const [username, setUsername] = useState(null);
  const [pelajaran, setPelajaran] = useState(null);
  const [timeOut, setTimeOut] = useState(false);
  const [benar, setBenar] = useState(0);
  const [salah, setSalah] = useState(0);
  const [questionNumber, setQuestionNumber] = useState(1);
  const [randomSoal, setRandomSoal] = useState([]);

  useEffect(async () => {
    const getSoal = await fetch(
      "https://raw.githubusercontent.com/carlosmaruao/icons/main/soal.json"
    );
    const resSoal = await getSoal.json();
    const getPelajaran = resSoal.filter((value) => {
      return value.pelajaran === pelajaran;
    });
    setRandomSoal(shuffleArray(getPelajaran));
  }, [pelajaran]);

  const mulaiLagi = () => {
    setPelajaran(null);
    setTimeOut(false);
    setBenar(0);
    setSalah(0);
    setQuestionNumber(1);
  };

  // const moneyPyramid = useMemo(
  //   () =>
  //     [
  //       { id: 1, amount: "$ 100" },
  //       { id: 2, amount: "$ 200" },
  //     ].reverse(),
  //   []
  // );

  // useEffect(() => {
  //   questionNumber > 1 &&
  //     setEarned(moneyPyramid.find((m) => m.id === questionNumber - 1).amount);
  // }, [questionNumber, moneyPyramid]);

  function shuffleArray(array) {
    let i = array.length - 1;
    for (; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      const temp = array[i];
      array[i] = array[j];
      array[j] = temp;
    }
    return array;
  }

  return (
    <div className="flex overflow-auto w-screen h-screen bg-blue-50">
      {/* {!username ? (
      <Start setUsername={setUsername} />
    ) : ( */}
      {!pelajaran ? (
        <Start setPelajaran={setPelajaran} />
      ) : (
        <>
          <div className="flex flex-col w-full h-full">
            {timeOut ? (
              <div className="flex flex-col gap-4 justify-center items-center h-screen">
                <div className="leading-3">hasil ujian soal </div>
                <div className="font-bold text-2xl -mt-4">{pelajaran}</div>
                <div>
                  Benar :{" "}
                  <span className="font-bold text-xl text-green-600">
                    {benar}
                  </span>
                </div>
                <div>
                  Salah :{" "}
                  <span className="font-bold text-xl text-red-600">
                    {salah}
                  </span>
                </div>
                <div>
                  <button
                    onClick={mulaiLagi}
                    className="bg-blue-500 text-white py-2 px-8 rounded-lg"
                  >
                    Mulai lagi
                  </button>
                </div>
              </div>
            ) : (
              <>
                {randomSoal.length ? (
                  <>
                    <div className="bg-blue-400  p-4 md:p-6">
                      <div className="flex justify-between">
                        <div className=" text-sm rounded-xl p-4 bg-blue-50  flex items-center justify-center">
                          Soal No {questionNumber}
                        </div>
                        <div className="flex flex-row">
                          <div className="flex mx-1 rounded-full text-white bg-green-600 w-[50px] h-[50px] justify-center items-center">
                            {benar}
                          </div>
                          <div className="flex mx-1 rounded-full text-white bg-red-600 w-[50px] h-[50px] justify-center items-center">
                            {salah}
                          </div>
                        </div>
                        {/* <Timer
                    setTimeOut={setTimeOut}
                    questionNumber={questionNumber}
                    data={randomSoal}
                    setQuestionNumber={setQuestionNumber}
                    setBenar={setBenar}
                    setSalah={setSalah}
                  /> */}
                      </div>
                    </div>
                    <div className=" p-4 md:p-12">
                      <Trivia
                        data={randomSoal}
                        questionNumber={questionNumber}
                        setQuestionNumber={setQuestionNumber}
                        setBenar={setBenar}
                        setSalah={setSalah}
                        setTimeOut={setTimeOut}
                      />
                    </div>
                  </>
                ) : (
                  <>
                    <div className="flex flex-col gap-4 h-full  justify-center items-center">
                      <div>Maaf saat ini soal {pelajaran} belum tersedia</div>
                      <button
                        onClick={mulaiLagi}
                        className="bg-blue-500 text-white py-2 px-8 rounded-lg"
                      >
                        Mulai lagi
                      </button>
                    </div>
                  </>
                )}
              </>
            )}
          </div>
        </>
      )}
    </div>
  );
}

export default App;
