import { useEffect, useState } from "react";
import { Markup } from "interweave";
import useSound from "use-sound";
import play from "../sounds/play.mp3";
import correct from "../sounds/correct.mp3";
import wrong from "../sounds/wrong.mp3";

export default function Trivia({
  data,
  questionNumber,
  setQuestionNumber,
  setBenar,
  setSalah,
  setTimeOut,
}) {
  const [question, setQuestion] = useState(null);
  const [selectedAnswer, setSelectedAnswer] = useState(null);
  const [className, setClassName] = useState("answer");
  const [letsPlay] = useSound(play);
  const [correctAnswer] = useSound(correct);
  const [wrongAnswer] = useSound(wrong);

  useEffect(() => {
    // letsPlay();
  }, [letsPlay]);

  useEffect(() => {
    let soal = data[questionNumber - 1];
    const shuffledPosts = shuffleArray(soal?.answers);
    let jawaban = soal?.answers;
    jawaban = shuffledPosts;
    setQuestion(soal);
  }, [data, questionNumber]);

  const delay = (duration, callback) => {
    setTimeout(() => {
      callback();
    }, duration);
  };

  const handleClick = (a) => {
    setSelectedAnswer(a);
    setClassName("answer active");
    delay(300, () => {
      setClassName(a.correct ? "answer correct" : "answer wrong");
    });

    delay(300, () => {
      delay(300, () => {
        if (questionNumber <= data.length) {
          if (a.correct) {
            setBenar((prev) => prev + 1);
          } else {
            setSalah((prev) => prev + 1);
          }

          if (questionNumber === data.length) {
            setTimeOut(true);
          } else {
            setQuestionNumber((prev) => prev + 1);
            setSelectedAnswer(null);
          }
        } else {
          setTimeOut(true);
        }
      });
    });
  };

  function shuffleArray(array) {
    let i = array?.length - 1;
    for (; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      const temp = array[i];
      array[i] = array[j];
      array[j] = temp;
    }
    return array;
  }

  const pilihan = ["A", "B", "C", "D"];

  return (
    <div className="flex flex-col gap-4">
      <div className="font-normal text-lg demo">
        {question?.image && (
          <img
            src={question.image}
            alt={question.question}
            className="w-full"
          />
        )}
        <Markup content={question?.question} />
      </div>
      <div className="flex flex-wrap  border-t border-gray-300 pt-4">
        {question?.answers.map((a, index) => (
          <div
            key={a.text}
            // className={selectedAnswer === a ? "w-full md:w-1/2" : "answer"}
            className="w-full md:w-[45%] flex items-start justify-start bg-blue-200 flex-row group p-4 m-2 hover:bg-blue-500 rounded-xl border cursor-pointer "
            onClick={() => !selectedAnswer && handleClick(a)}
          >
            <div className="font-bold  px-4 flex group-hover:text-white">
              {pilihan[index]}
            </div>
            <div className="text-lg flex group-hover:text-white leading-5">
              {a?.image && <img src={a.image} alt={a.index} />}
              <Markup content={a.text} />
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
